


















































[v-cloak] 
  display: none;


.parallaxWrapper 
    display: grid 
    grid-template-columns: 1fr
    .paral
        grid-row-start: 1;
        grid-column-start: 1;
    img 
        min-width: 100%
        &.last
            z-index: 8
       
.herotext 
    position: absolute
    padding: 6rem 2rem
    @media screen and (min-width: 1080px)
        padding: 7rem 4rem
        div 
            width: 85%
            media screen and (min-width: 1600px)
                width: 60%
